@import url(https://fonts.googleapis.com/css?family=Rubik);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Rubik';
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  /* height: 250px; */
  text-align: center;
}
.container {
    text-align: center;
}

.header {
    margin-top: 0;
    padding-top: 40px;
    color: white;
}

.search-bar {
    width: 50%;
    height: 40px;
    border-radius: 10px 0 0 10px;
    padding: 0 10px;
}

.search-bar:focus {
    outline: none;
}

.button {
    height: 43px;
    width: 40px;
    background-color: #2b2b2b;
    border: none;
    padding: 0;
    border-radius: 0 10px 10px 0;
    cursor: pointer;
}

.button:focus {
    outline: none;
}

@media only screen and (max-width: 600px) {
    .header {
        padding-top: 25px;
    }

    .search-bar {
        width: 73%;
    }
}
.details-container {
    height: 100px;
    width: 80%;
    background-color: white;
    border: 1px solid #2b2b2b;
    margin: 0 auto;
    margin-top: 70px;
    border-radius: 10px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 0 10px;
}

.details {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.caption {
    font-size: 11px;
    font-weight: 700;
    color: #969696;
}

.value {
    font-weight: 700;
    font-size: 20px;
    text-align: left;
}

.divider {
    height: 60px;
    width: 1px;
    background: #969696;
    margin: 0 10px;
}

@media only screen and (max-width: 600px) {
    .details-container {
        flex-direction: column;
        height: 100%;
        padding-top: 15px;
        padding-bottom: 15px;
        margin-top: 20px;
    }

    .divider {
        display: none;
    }

    .details {
        margin-top: 10px;
        margin-bottom: 10px;
        align-items: center;
    }
}
.map-container {
    margin-top: -54px;
    position: relative;
    z-index: -1;
    height: calc(100vh - 250px);
}

.leaflet-container {
    height: 100%;
}

@media only screen and (max-width: 600px) {
    .map-container {
        margin-top: -147px;
    }
}
